@import "../colours.scss";

.body,
html {
  background-color: black;
}

.wrapper {
  background: linear-gradient(
    to bottom right,
    rgba($main, 0.6) 0%,
    rgba($secondary, 0.40) 100%
  );
  background-position: center;
  background-size: cover;
  position: absolute;
  background-attachment: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.bg-bubbles {
  width: 100%;
  height: 100%;

  li {
    position: absolute;
    list-style: none;
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background: rgba($secondary, 0.032);
    bottom: -160px;

    -webkit-animation: square 25s infinite;
    animation: square 25s infinite ease-in-out;

    -webkit-transition-timing-function: linear;
    transition-timing-function: linear;

    &:nth-child(1) {
      left: 10%;
    }

    &:nth-child(2) {
      left: 20%;

      width: 80px;
      height: 80px;

      animation-delay: 0s;
      animation-duration: 14s;
    }

    &:nth-child(3) {
      left: 25%;
      animation-delay: 1s;
    }

    &:nth-child(4) {
      left: 40%;
      width: 60px;
      height: 60px;

      animation-duration: 22s;

      background-color: fade(white, 25%);
    }

    &:nth-child(5) {
      left: 70%;
    }

    &:nth-child(6) {
      left: 80%;
      width: 120px;
      height: 120px;

      animation-delay: 1s;
      background-color: fade(white, 20%);
    }

    &:nth-child(7) {
      left: 32%;
      width: 160px;
      height: 160px;

      animation-delay: 4s;
    }

    &:nth-child(8) {
      left: 55%;
      width: 20px;
      height: 20px;

      animation-delay: 12s;
      animation-duration: 40s;
    }

    &:nth-child(9) {
      left: 25%;
      width: 10px;
      height: 10px;

      animation-delay: 0s;
      animation-duration: 40s;
      background-color: fade(white, 30%);
    }

    &:nth-child(10) {
      left: 90%;
      width: 160px;
      height: 160px;

      animation-delay: 8s;
    }
  }
}

@-webkit-keyframes square {
  0% {
    transform: translateY(0);
  }
  93% {
    opacity: 1;
  }
  100% {
    transform: translateY(-700px) rotate(600deg);
    opacity: 0;
  }
}
@keyframes square {
  0% {
    transform: translateY(0);
  }
  93% {
    opacity: 1;
  }
  100% {
    transform: translateY(-700px) rotate(600deg);
    opacity: 0;
  }
}
